let innerAudioContext = uni.createInnerAudioContext();
import uniPopup from "../../../components/uni-popup/uni-popup.vue"
import dragball from '../../../components/drag-ball/drag-ball.vue'
import {
	CALLRECORDDETAILS,
	ADDBLACKLIST,
	DELCALL
} from "../../../utils/api.js"
export default {
	components: {
		dragball,
		uniPopup
	},
	filters: {
		formateTel: function(value) {
			var pat = /(\d{3})\d*(\d{4})/
			var mobile = String(value).replace(pat, '$1****$2');
			return mobile
		},
		chatFileter:function(value){
			var result
			if(value == '香蕉来电已拒接'){
				result = '魔小秘已拒接'
				return result
			}else{
				result = value
				return result
			}
		}
	},
	data() {
		return {
			audioAction: {
				method: 'pause'
			},
			recShow: false,
			playIcon: false,
			iconPlay: false,
			currentTime: '00:00', //当前播放时间
			durationTime: '00:00', //总时长
			current: 0, //slider当前进度
			loading: false, //是否处于读取状态
			paused: true, //是否处于暂停状态
			seek: false, //是否处于拖动状态
			duration: 100,
			itemStart: '',
			recordId: "",
			detailData: "",
			totalCur: 0,
			totalStart: 0,
			totalPlay: false,
			src: "",
			startTime: 0,
			itemSrc: "",
			blackShow: false
		}
	},
	onLoad(options) {
		this.recordId = JSON.parse(options.id); // 字符串转对象
		this.initData()
	},
	onHide() {
		innerAudioContext.stop()
	},
	onUnload() {
		innerAudioContext.stop()
	},
	watch: {
		//监听总时长改变
		duration(e) {
			this.durationTime = this.format(e)
		},
		//监听当前进度改变
		current(e) {
			this.currentTime = this.format(e)
		}
	},
	methods: {
		// 初始化数据
		initData() {
			const params = {
				recordId: this.recordId
			}
			this.$request.url_request(CALLRECORDDETAILS, params, "GET", res => {
				this.detailData = JSON.parse(res.data).data
				this.src = JSON.parse(res.data).data.audioOssUrl;
				for (let i = 0; i < this.detailData.recordMessageList.length; i++) {
					this.detailData.recordMessageList[i].isClick = false;
				}
				this.itemSrc = JSON.parse(res.data).data.userAudioOssUrl
				this.$forceUpdate();
			}, err => {})
		},
		// 拨打电话
		callPhone() {
			uni.makePhoneCall({
				phoneNumber: this.detailData.callerMobile
			})
		},
		// 点击按钮播放
		playItemRecord(start, end, index) {
			let _inn = uni.createInnerAudioContext();
			var j = 0
			for (let i = 0; i < this.detailData.recordMessageList.length; i++) {
				if (index === i) {
					j = i
					this.detailData.recordMessageList[index].isClick = !this.detailData.recordMessageList[index].isClick
					this.$forceUpdate();
					if (this.detailData.recordMessageList[index].isClick == true) {
						console.log(this.detailData.recordMessageList[index].isClick)
						console.log('开始播放1');
						var that = this;
						_inn.src = this.itemSrc
						console.log('开始播放2');
						var flag = 0;
						_inn.play();
						_inn.onCanplay(function() {
							console.log('可以播放');
							if (flag == 1) {
								return;
							} else {
								_inn.seek(start / 1000);
								_inn.onSeeking(function() {
									// console.log('正在跳转');
								})
								_inn.onSeeked(function() {
									// console.log('完成跳转');
									flag = 1;
									var inerval = setInterval(() => {
										if (_inn.currentTime >= end / 1000) {
											_inn.stop()
											_inn.onStop()
											that.paused = true
											that.detailData.recordMessageList[j].isClick = false
											that.$forceUpdate();
										}
									}, 100)
									_inn.onError((res) => {
										that.title = JSON.stringify(res);
									});
								})
							}

						});
						_inn.onError(function() {
							console.log('播放失败');
						})

					} else {
						console.log(this.detailData.recordMessageList[index].isClick)
						_inn.pause()
						_inn.onPause(function(){
							console.log('暂停')
						})
					}
				}

			}
		},
		// 进度条滑动
		sliderChange(e) {
			innerAudioContext.seek(e.detail.value)
		},
		//格式化时长
		format(num) {
			return '0'.repeat(2 - String(Math.floor(num / 60)).length) + Math.floor(num / 60) + ':' + '0'.repeat(2 - String(
				Math.floor(num % 60)).length) + Math.floor(num % 60)
		},
		//播放音频
		playRecord() {
			this.playIcon = !this.playIcon
			var that = this
			this.totalPlay = !this.totalPlay
			if (this.src == '') {
				uni.showToast({
					title: '暂无通话录音',
					icon: 'none',
					duration: 1000
				})
			} else {
				that.recShow = true
				if (that.totalPlay) {
					this.current = 0
					var flag = 0
					var that = this
					innerAudioContext.autoplay = true //开始自动播放
					innerAudioContext.src = this.src
					// 音频进度更新事件
					innerAudioContext.onTimeUpdate(() => {
						if (!this.seek) {
							this.current = innerAudioContext.currentTime
						}
					})
					// 音频播放
					innerAudioContext.onPlay(() => {
						this.durationTime = this.format(innerAudioContext.duration) //当前总时长
						this.duration = innerAudioContext.duration
						this.paused = false
						this.loading = false
					})
					innerAudioContext.onCanplay(function() {
						if (flag == 1) {
							return
						} else {
							innerAudioContext.seek(that.startTime / 1000)
							innerAudioContext.onSeeked(function() {
								flag = 1
							})
							innerAudioContext.onEnded(function() {
								console.log('自然播放结束')
								that.playIcon = false
							})
						}
					})
					// 音频暂停
					innerAudioContext.onPause(() => {
						this.paused = true
					})
					// 音频结束
					innerAudioContext.onEnded(() => {
						this.current = 0
						this.recShow = false
					})
					//音频完成更改进度事件
					innerAudioContext.onSeeked(() => {
						this.seek = false
					})
				} else {
					innerAudioContext.pause()
					innerAudioContext.onPause(function() {
						that.startTime = innerAudioContext.currentTime * 1000
						console.log('播放展厅')
					})
				}


			}

		},
		//添加联系人
		addContact() {
			uni.navigateTo({
				url: "../../CustomPage/AddContact/AddContact?mobile=" + this.detailData.callerMobile
			})
		},
		// 添加黑名单
		addBlack() {
			var _this = this
			uni.showModal({
				title: '提示',
				content: '确认加入黑名单？',
				duration: 5000,
				success: function(res) {
					if (res.confirm) {
						const params = {
							mobile: _this.detailData.callerMobile,
							name: _this.detailData.nickName
						}
						_this.$request.url_request(ADDBLACKLIST, params, "GET", res => {
							uni.showToast({
								title: '添加成功',
								icon: 'success',
								duration: 1000
							})
						}, err => {})
					} else if (res.cancel) {}

				}
			})
		},
		// 删除通话记录
		delCall(){
			var _this = this
			uni.showModal({
				title: '提示',
				content: '确认删除通话记录？',
				duration: 5000,
				success: function(res) {
					if (res.confirm) {
					const params = {
						callInstanceId: _this.recordId
					}
					_this.$request.url_request(DELCALL,params,'GET',res =>{
						uni.showToast({
							title:'删除成功',
							icon:'success',
							duration:1200
						})
						setTimeout(() =>{
							uni.navigateTo({
								url:'../callList'
							})
						},1200)
					},err=>{})
					} else if (res.cancel) {}
			
				}
			})
		},
		// 返回上页
		goback() {
			innerAudioContext.stop()
			uni.navigateBack({
				delta: 1
			})
		},
		// 更多
		more(){
			 this.$refs.popup.open()
		}
	}
}
